export default {
	data() {
		return {
			form:{
				address:'',	   //目标用户id
				name:'',			//金额
				subscript:'',	//支付密码

			},
			ratio:''
		};
	},
	onLoad:function(){
		
	},
	methods:{
		
		async submit(){
			uni.showToast({
				
				title: '格式不符',
				icon:'none',
				duration: 1000
			});
			return
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.get('/deal/signin_shift',{data: this.form})
			uni.hideLoading()
			
			uni.showToast({
				title: '轉賬成功',
				icon: 'none',
				duration: 1000
			});
			setTimeout(function() {
				uni.navigateBack({
					delta: -1
				});
			}, 2000);

		},
		scanCodeToken:function(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
					this.form.address = res.result
			    }
			});
		},
		scanCodeNode: function(){
			uni.scanCode({
			    success: function (res) {
			        this.form.userCode = res.result
			    }
			});
		}
	}
}
